import React from 'react'

export default function revenueupdate() {
  return (
    <section className="bg-white shadow-lg rounded-lg p-6 mb-8">
    <h3 className="text-xl font-bold text-gray-800 mb-4">Revenue Update</h3>
    <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
      <div className="w-full h-64 bg-gray-100 rounded-lg flex items-center justify-center text-gray-400">
        Revenue Chart Placeholder
      </div>
      <ul className="space-y-4">
        {[
          {
            label: "Online Sales",
            value: "$80,000",
            percentage: "65% of total",
          },
          {
            label: "In-Store Sales",
            value: "$44,567",
            percentage: "35% of total",
          },
        ].map(({ label, value, percentage }, index) => (
          <li
            key={index}
            className="bg-gray-50 p-4 rounded-lg shadow-md flex justify-between items-center"
          >
            <div>
              <h4 className="text-lg font-semibold text-gray-700">{label}</h4>
              <p className="text-gray-500 text-sm">{percentage}</p>
            </div>
            <span className="text-lg font-bold text-gray-800">{value}</span>
          </li>
        ))}
      </ul>
    </div>
  </section>
  )
}
