import React from 'react'

export default function inventorystatus() {
  return (
    <div>
        <section className="bg-white shadow-lg rounded-lg p-6 mb-8">
        <h3 className="text-xl font-bold text-gray-800 mb-4">Inventory Status</h3>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {[
            {
              item: "T-Shirts",
              stock: "120 units",
              status: "In Stock",
              bg: "bg-green-100",
              color: "text-green-600",
            },
            {
              item: "Sneakers",
              stock: "15 units",
              status: "Low Stock",
              bg: "bg-yellow-100",
              color: "text-yellow-600",
            },
            {
              item: "Caps",
              stock: "0 units",
              status: "Out of Stock",
              bg: "bg-red-100",
              color: "text-red-600",
            },
          ].map(({ item, stock, status, bg, color }, index) => (
            <div
              key={index}
              className={`p-4 rounded-lg shadow-md flex items-center justify-between ${bg} ${color}`}
            >
              <div>
                <h4 className="text-lg font-semibold">{item}</h4>
                <p className="text-sm">{status}</p>
              </div>
              <span className="text-lg font-bold">{stock}</span>
            </div>
          ))}
        </div>
      </section>
    </div>
  )
}
